import { Link, useStaticQuery, graphql } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import './header.scss'


const RecentArticles = (props) => {
	const { allMarkdownRemark } = useStaticQuery(graphql`
		query RecentPagesQuery2 {
			allMarkdownRemark(
				sort: { order: DESC, fields: [frontmatter___date] }
				limit: 6
			) {
				edges {
					node {
						frontmatter {
							date(formatString: "MMMM DD, YYYY")
							path
							title
						}
					}
				}
			}
		}
	`)
	const Articles = allMarkdownRemark.edges.map(e => {
		return {
			date: e.node.frontmatter.date,
			path: e.node.frontmatter.path,
			title: e.node.frontmatter.title
		}
	})

	const AddFade = () => {
		return Articles.length === 6 ? 'recentArticlesFade' : '';
	}

	return (
		<nav id="recentArticles" className={[props.className, AddFade()].join(' ')}>
			<ol>
				{Articles.map(e =>
					<li key={e.path}>
						<Link to={e.path}>
							{e.title}
						</Link>
						<p style={{margin: `0`}}>{e.date}</p>
					</li>
				)}
			</ol>
		</nav>
	)
}

const Header = ({ siteTitle, children }) => (
	<header>
		<h1>
			<Link to="/">
				{siteTitle}
			</Link>
		</h1>
		<nav id="navLinks">
			<Link to="/gallery">Gallery</Link> |
			<Link to="/articles">Articles</Link> |
			<Link to="/contact">Contact</Link>
		</nav>
		<h4>Recent Articles:</h4>
		<RecentArticles className="recentArticles"/>
		<footer>
			<a href="https://github.com/ajmar/etylermoss">
				View the project on Github
				<p>ajmar/etylermoss</p>
			</a>
		</footer>
	</header>
)

Header.propTypes = {
	siteTitle: PropTypes.string,
}

export default Header
